<template>
  <div v-if="this.finalContent">
    <Newcollapse>
      <Newcollapsepanel
        v-for="(item, index) in this.finalContent"
        :key="index"
        :header="`Add ${item.add}`"
      >
        <Newcollapse>
          <Newcollapsepanel
            v-for="(spec, index2) in item.list"
            :key="index2"
            :header="spec.description"
            :applySpec="spec"
          >
            <div
              class="applypanel"
              @click="handleApply(spec)"
              @mouseenter="previewSpec(spec)"
              @mouseleave="restorePreview"
            >
              <div class="applypanelcontent">
                <a-icon type="bulb" class="icon applypanelicon"/>
                <span class="applypaneltext"> Apply </span>
              </div>
            </div>
            <Newcollapse>
              <Newcollapsepanel
                header="Alternatives (Rearrange current attributes)"
              >
                <alterpanel :spec="spec"/>
              </Newcollapsepanel>
              <Newcollapsepanel
                header="Variations (Add more attributes)"
              >
                <Varunit :spec="spec" />
              </Newcollapsepanel>
            </Newcollapse>
          </Newcollapsepanel>
        </Newcollapse>
      </Newcollapsepanel>
    </Newcollapse>
  </div>
</template>

<script>
import Utils from "@/utils.js";
import { mapState, mapActions } from "vuex";
import alterpanel from "../alterpanel/Index.vue";
import { transform } from "rigel-tools";
export default {
  name: "Varunit",
  props: {
    content: {
      type: Array,
      default: null,
    },
    spec: {
      type: Object,
      default: undefined,
    },
  },
  data() {
    return {
      newContent: null,
      alterSuggestion: [],
    };
  },
  computed: {
    ...mapState(["attrInfo", "rawRelations", "canSuggest"]),
    finalContent() {
      if (this.content) return this.content;
      return this.newContent;
    },
  },
  watch: {
    spec(val, oldval) {
      if(val != oldval) {
        this.newContent = this.calcExplore(val);
        this.$forceUpdate();
      }
    }
  },
  mounted() {
    if (!this.content && this.spec) {
      this.newContent = this.calcExplore(this.spec);
    }
  },
  methods: {
    ...mapActions(["storeCurrentTable", "storeNewSpec", "restoreCurrentState", "storeCurrentState", "storeCanSuggest"]),
    calcExplore(spec) {
      // calculation
      try {
        let row_header = Utils.specObj2List(spec.row_header);
        let column_header = Utils.specObj2List(spec.column_header);
        let body = Utils.specObj2List(spec.body);
        console.log(row_header);
        let exploreSpec = Utils.genExploreSpec(
          row_header,
          column_header,
          body,
          this.attrInfo
        );
        return exploreSpec;
      } catch (err) {
        console.log(err.message);
        this.$message.error(err.message);
      }
      // return ([
      //   {
      //       "add": "union(a,b)",  // string
      //       "list": [
      //           {   // spec
      //               "row_header": null,
      //               "column_header": null,
      //               "body": null,
      //               "description": "(a), (b) => (c)",
      //           },
      //       ]
      //   }
      // ]);
    },
    handleApply(spec, isPreview) {
      if (!spec) return;
      if(!isPreview) {
        if(!this.canSuggest) {
          this.restoreCurrentState();
        }
        this.storeCanSuggest(true);
      }
      let sch = {
        data: this.rawRelations,
        target_table: [spec],
      };
      console.log(sch);
      try {
        let res = transform(sch)[0];
        // console.log(res);
        for (let i = 0; i < res.length; i++) {
          for (let j = 0; j < res[i].length; j++) {
            if (res[i][j]) {
              let tmp = {};
              tmp.source = res[i][j].source;
              tmp.value = res[i][j].value ? res[i][j].value : res[i][j];
              res[i][j] = tmp;
            }
          }
        }
        // console.log(res);
        this.storeCurrentTable(res);
        this.storeNewSpec(spec);
      } catch (err) {
        this.$message.error("Illegal specification!");
      }
    },
    previewSpec(spec) {
      this.storeCurrentState();
      this.storeCanSuggest(false);
      this.handleApply(spec, true);
    },
    restorePreview() {
      if(!this.canSuggest) {
        this.restoreCurrentState();
      }
    }
  },
  components: {
    alterpanel
  },
};
</script>